.header {
    height: 70px;
    background-color: #20232a;
    display: flex;
    justify-content: center;
    position: fixed;
    width: 100%;
    z-index: 2;
    opacity: 0.9;
}

.container {
    width: 80%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}



@media screen and (max-width: 600px) {
    .container {
        width: 90%;

    }
}